}
.bootstrap-tagsinput .tag {
  padding: 5px 12px;
  border-radius: 2px;
  line-height: 37px;
  margin-top: 5px;
  margin-right: 5px;
}
.bootstrap-tagsinput .tag [data-role=remove] {
  margin-right: -5px;
}
.bootstrap-tagsinput .tag [data-role=remove]:after {
  content: "";
  padding: 0 2px;
  font-family: "feather" !important;
}
